.feedback-form {
  max-width: 480px;
  background-color: #191818;
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  color: #fff;
  flex-direction: column;
  margin-bottom: 16px;
  font-family: Raleway;
  font-size: 20px;
  font-weight: 600;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

input, textarea {
  background-color: #191818;
  border: 2px solid #f37055;
  border-radius: 4px;
}

input, textarea, select {
  color: #fff;
}

#btn_submit {
  color: #fff;
  background-color: #191818;
  border: 2px solid #f37055;
  border-radius: 4px;
  font-size: 20px;
}

